/*
ajax
 */
import axios from 'axios'
export function getGoods() {
  return axios.get('/api2/goods')  // promise
}

export function getRatings() {
  return axios.get('/api2/ratings')
}


export function getSeller() {
  return axios.get('/api2/seller')
}


