export const ratingMixin = {
  data () {
    return {
      onlyContent: true,
      selectType: 2  // 2:   1:   0: 
    }
  },

  methods: {
    toggleOnlyContent() {
      this.onlyContent = !this.onlyContent
      // 
      this.$nextTick(() => {
        this.scroll.refresh()
      })
    },

    setSelectType(selectType) {
      this.selectType = selectType
      // 
      this.$nextTick(() => {
        this.scroll.refresh()
      })
    }
  },

  computed: {
    filterRatings() {
      const ratings = this.ratings || this.food.ratings

      if (!ratings) {  // , 
        return []
      }

      const {onlyContent, selectType} = this
      return ratings.filter(rating => {
        /*
        onlyContent: true/false   : rating.text
        selectType: 0/1/2         : rating.rateType  0/1
         */
        // 1: onlyContentfalse, , onlyContenttrue, 
        !onlyContent || rating.text.length > 0
        // 2: selectType2, rateType, selectType0/1, rateTypeselectType
        selectType === 2 || selectType === rating.rateType
        return (!onlyContent || rating.text.length > 0) && (selectType === 2 || selectType === rating.rateType)
      })
    }
  }
}





